@import "screens/shared/styles/colors.sass";

.plashque {
  background-color: rgba($reject-accent-color, 0.9);
  margin: 0 auto;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  max-height: 18rem;
  color: $text-color;
  max-width: 90%;
  box-sizing: border-box;
  padding: 0.8rem 2rem;
  font-size: 1.2rem;
  text-align: center;
  font-weight: 300; }

.buttonsContainer > div {
  margin: 0.3rem 0;
  &>button {
    min-width: 0 !important;
    min-height: 0 !important; } }

.snackbar {
  div {
    background-color: $reject-accent-color !important;
    display: flex;
    align-items: center;
    font-size: 1.2rem; }
  i {
    margin-right: 1rem;
    font-size: 2rem; } }
